import Vue from 'vue';
import Router from 'vue-router';

// 
import Approval from './views/approval/Approval.vue'; //
import MyApproval from './views/approval/MyApproval.vue'; //
import MyInitiated from './views/approval/MyInitiated.vue'; //
import CarbonCopy from './views/approval/CarbonCopy.vue'; //
import NewApply from './views/approval/NewApply.vue'; //
import Contract from './views/approval/Contract.vue'; //
import DepartmentReview from './views/approval/DepartmentReview.vue'; //
import ApprovalComments from './views/approval/ApprovalComments.vue'; //

//
import CheckPersonnel from './views/contactPerson/CheckPersonnel.vue'; //
import RadioPersonnel from './views/contactPerson/RadioPersonnel.vue'; //
import PersonalDesc from './views/contactPerson/PersonalDesc.vue'; //


//
import HelpCenter from './views/helpCenter/HelpCenter.vue'; //
import Directions from './views/helpCenter/Directions.vue'; //
import CommonProblem from './views/helpCenter/CommonProblem.vue'; //
import ProblemDesc from './views/helpCenter/ProblemDesc.vue'; //
import UpdateLog from './views/helpCenter/UpdateLog.vue'; //
import NewFn from './views/helpCenter/NewFn.vue'; //

//
import InternalNews from './views/enterpriseNews/internalNews/InternalNews.vue'; //
import AcitveDesc from './views/enterpriseNews/internalNews/AcitveDesc.vue'; //


//
import Index from './views/Index.vue'; //

Vue.use(Router);

export default new Router({
  mode: 'hash',
  base: '/dist/',
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
      meta: {
        title: ''
      }
    },
    {
      path: '/internalNews',
      name: 'internalNews',
      component: InternalNews,
      meta: {
        title: ''
      }
    },
    {
      path: '/activeDesc',
      name: 'activeDesc',
      component: AcitveDesc,
      meta: {
        title: ''
      }
    },

    /**
     * 
     */
    {
      path: '/helpCenter',
      name: 'helpCenter',
      component: HelpCenter,
      meta: {
        title: ''
      }
    },
    {
      path: '/directions',
      name: 'directions',
      component: Directions,
      meta: {
        title: ''
      }
    },
    {
      path: '/commonProblem',
      name: 'commonProblem',
      component: CommonProblem,
      meta: {
        title: ''
      }
    },
    {
      path: '/problemDesc',
      name: 'problemDesc',
      component: ProblemDesc,
      meta: {
        title: ''
      }
    },
    {
      path: '/updateLog',
      name: 'updateLog',
      component: UpdateLog,
      meta: {
        title: ''
      }
    },
    {
      path: '/newFn',
      name: 'newFn',
      component: NewFn,
      meta: {
        title: ''
      }
    },

    /**
     * 
     */
    {
      path: '/approval',
      name: 'approval',
      component: Approval,
      meta: {
        title: ''
      }
    },
    {
      path: '/myApproval',
      name: 'myApproval',
      component: MyApproval,
      meta: {
        title: ''
      }
    },
    {
      path: '/myInitiated',
      name: 'myInitiated',
      component: MyInitiated,
      meta: {
        title: ''
      }
    },
    {
      path: '/carbonCopy',
      name: 'carbonCopy',
      component: CarbonCopy,
      meta: {
        title: ''
      }
    },
    {
      path: '/newApply',
      name: 'newApply',
      component: NewApply,
      meta: {
        title: '',
        keepAlive: true
      }
    },
    {
      path: '/contract',
      name: 'contract',
      component: Contract,
      meta: {
        title: ''
      }
    },
    {
      path: '/departmentReview',
      name: 'departmentReview',
      component: DepartmentReview,
      meta: {
        title: ''
      }
    },
    {
      path: '/approvalComments',
      name: 'approvalComments',
      component: ApprovalComments,
      meta: {
        title: ''
      }
    },

    /**
     * 
     */
    {
      path: '/personalDesc',
      name: 'personalDesc',
      component: PersonalDesc,
      meta: {
        title: ''
      }
    },
    {
      path: '/checkPersonnel',
      name: 'checkPersonnel',
      component: CheckPersonnel,
      meta: {
        title: '()'
      }
    },
    {
      path: '/radioPersonnel',
      name: 'radioPersonnel',
      component: RadioPersonnel,
      meta: {
        title: '()'
      }
    },
  ],
});
